.notifications-menu {
    left: -48px !important;
}

.notification-header {
    font-size: 85%;
    padding: 0px 13px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #ddd;
    cursor: initial;
    .action {
        color: #007bff;
        text-decoration: none;
        background-color: transparent;
        cursor: pointer;
        &:hover {
            text-decoration: underline;
        }
    }
}

.notifications-menu {
    width: 100vw;
}

.notification {
    font-size: 90%;
    border-bottom: 1px solid #ddd;

    .miniature {
        display: inline-block;
        margin: 0 12px 0 4px;
        vertical-align: top;

        img {
            height: 60px;
            width: 60px;
        }
    }

    .infos {
        display: inline-block;

        .description, .date {
            white-space: normal;
            padding-right: 5px;
        }

        .date {
            font-size: 90%;
        }
    }
}
